.text-animate{
    display:  inline-block;
    opacity: 0;
    animation: bounceIn 1s 1s;
    animation-fill-mode: forwards;
    min-width: 10px;
    letter-spacing: 3px;

   
}
.text-animate-hover{
    min-width: 10px;
    display: inline-block;
    animation-fill-mode: both;
    letter-spacing: 3px;
    &:hover{
        animation: rubberBand 1s;
        color :#ffd700;
    }
}
@for $i from 1 through 44{
    .text-animate._#{$i}{
        animation-delay: #{$i/10}s;}

}